
.label {
  display: block;
  line-height: 1;
  font-size: 1rem;
  padding-bottom: 0.5rem;
}

.input{
  width: 100%;
  font-size: 1rem;
  padding: 0.8rem;
  border: 1px solid #eee;
  border-radius: 0.4rem;
  background: #eee;
  transition: 0.3s;
}

.input:hover, .input:focus{
outline: none;
border-color: #fb1;
background-color: white;
box-shadow: 0 0 0 3px #fea;
}

.wrapper{
  margin-bottom: 1rem;
}

.error{
  color: #f31;
  font-size: 0.875rem;
  margin-top: 0.25rem;
}