












.modal{
  position: fixed;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.3);
  top: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  padding: 2rem calc(4rem + 15px) 2rem 4rem;
}
.modal.close{
  display: none;
}

@media (max-width: 40rem){
  .modal{
    padding: 2rem calc(2rem + 15px) 2rem 2rem;
  }
}
